    color: $color;          //perfecto para usar los mediaQueries
} */                        //todo los que comiensa con @ son directivas de SCSS

@mixin phone {
    @media(min-width: v.$phone){
        @content; //para que soporte nuestro bloque de codigo en el layout 
    }
}

@mixin tablet {
    @media(min-width: v.$tablet){
        @content; 
    }
}

@mixin desktop {
    @media(min-width: v.$desktop){
        @content; 
    }
}

@mixin desktopXL {
    @media(min-width: v.$desktopXL){
        @content; 
    }
}

@mixin grid ($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetear-lista{
    margin: 0;
    padding: 0;
    list-style: none;
}